/* Добавляем стили для кнопок корзины */
.cart-button {
  position: absolute;
  top: -170px;
  right: 10px;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 10;
}

.cart-button:hover {
  background: #4caf50;
  color: white;
  transform: scale(1.1);
}

.cart-button.in-cart {
  background: #4caf50;
  color: white;
}

.card-buttons {
  position: relative;
  width: 100%;
  height: 0;
}

.food-image {
  position: relative;
  margin-top: 15px;
  text-align: center;
}

.favorite-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(255, 255, 255, 0.9);
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 10;
}

.favorite-button:hover {
  background: #ffeb3b;
  transform: scale(1.1);
}

.header-actions {
  position: absolute;
  top: 20px;
  right: 20px;
}

.cart-icon-header {
  background: #fff;
  border: 2px solid #2e7d32;
  border-radius: 20px;
  padding: 8px 15px;
  color: #2e7d32;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cart-icon-header:hover {
  background: #2e7d32;
  color: white;
}

/* Остальные стили остаются без изменений */
.main-page {
  font-family: 'Arial', sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f8f8f8;
}

.header {
  background-color: #2e7d32;
  color: white;
  padding: 30px 20px;
  text-align: center;
  border-radius: 8px;
  margin-bottom: 30px;
  position: relative;
}

.header h1 {
  margin: 0;
  font-size: 2.2rem;
}

.highlight {
  color: #ffeb3b;
}

.restaurant-section,
.food-section {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  margin-bottom: 30px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

h2 {
  color: #2e7d32;
  border-bottom: 2px solid #e0e0e0;
  padding-bottom: 10px;
  margin-top: 0;
}

.restaurant-card {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 15px;
}

.restaurant-info {
  flex: 1;
}

.restaurant-info h3 {
  margin: 0 0 10px 0;
  color: #333;
}

.restaurant-info p {
  margin: 5px 0;
  color: #666;
}

.rating {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.rating-icon {
  margin-right: 5px;
  font-size: 1.2rem;
}

.rating-value {
  font-weight: bold;
}

.restaurant-image img,
.food-image img {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 8px;
}

.food-items {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 15px;
}

.food-card {
  background: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  flex: 1;
  min-width: 250px;
  position: relative;
}

.food-card h3 {
  margin: 0 0 10px 0;
  color: #333;
}

.food-card p {
  margin: 5px 0;
  color: #666;
}

.loading {
  text-align: center;
  padding: 50px;
  font-size: 1.2rem;
}