.search-page {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
}

.search-title {
    text-align: center;
    color: #333;
    margin-bottom: 30px;
    font-size: 2rem;
}

/* Стили для табов */
.tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    border-bottom: 2px solid #eee;
}

.tab-button {
    padding: 12px 30px;
    border: none;
    background: none;
    font-size: 1.1rem;
    font-weight: 600;
    color: #666;
    cursor: pointer;
    transition: all 0.3s ease;
    border-bottom: 3px solid transparent;
    margin: 0 10px;
}

.tab-button:hover {
    color: #333;
}

.tab-button.active {
    color: #2e7d32;
    border-bottom: 3px solid #2e7d32;
}

/* Стили для загрузки */
.loading {
    text-align: center;
    font-size: 1.2rem;
    color: #666;
    padding: 50px;
}

.no-results {
    text-align: center;
    font-size: 1.2rem;
    color: #999;
    padding: 50px;
}

/* Стили для результатов блюд */
.dishes-results {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 25px;
    padding: 20px 0;
}

.dish-item {
    background: white;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    flex-direction: column;
    height: fit-content;
}

.dish-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.dish-info h3 {
    margin: 0 0 10px 0;
    color: #333;
    font-size: 1.3rem;
}

.restaurant {
    color: #2e7d32;
    font-weight: 600;
    margin: 5px 0;
    font-size: 0.95rem;
}

.description {
    color: #666;
    line-height: 1.4;
    margin: 10px 0;
    font-size: 0.9rem;
}

.dish-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 15px;
}

.price {
    font-size: 1.4rem;
    font-weight: bold;
    color: #2e7d32;
}

.dish-image {
    width: 80px;
    height: 80px;
    border-radius: 8px;
    overflow: hidden;
}

.dish-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Стили для результатов ресторанов */
.restaurants-results {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 25px;
    padding: 20px 0;
}

.restaurant-item {
    background: white;
    border-radius: 12px;
    padding: 20px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.restaurant-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.restaurant-info {
    flex: 1;
    margin-right: 20px;
}

.restaurant-info h3 {
    margin: 0 0 10px 0;
    color: #333;
    font-size: 1.3rem;
}

.rating {
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.rating-icon {
    margin-right: 5px;
}

.rating-value {
    font-weight: 600;
    color: #333;
}

.restaurant-image {
    width: 100px;
    height: 100px;
    border-radius: 8px;
    overflow: hidden;
    flex-shrink: 0;
}

.restaurant-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* Разделитель */
.divider {
    height: 1px;
    background: #eee;
    margin: 15px 0 0 0;
}

/* Адаптивность */
@media (max-width: 768px) {
    .search-page {
        padding: 15px;
    }
    
    .search-title {
        font-size: 1.5rem;
    }
    
    .tabs {
        flex-direction: column;
        align-items: center;
    }
    
    .tab-button {
        width: 200px;
        margin: 5px 0;
    }
    
    .dishes-results,
    .restaurants-results {
        grid-template-columns: 1fr;
        gap: 20px;
    }
    
    .restaurant-item {
        flex-direction: column;
        text-align: center;
    }
    
    .restaurant-info {
        margin-right: 0;
        margin-bottom: 15px;
    }
    
    .dish-details {
        flex-direction: column;
        gap: 15px;
    }
}