.restaurant-page {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  min-height: 100vh;
}

/* Header Styles */
.restaurant-header {
  background: white;
  border-radius: 16px;
  margin-bottom: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
}

.restaurant-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #2e7d32, #4caf50, #81c784);
}

.header-top {
  padding: 20px 30px 0;
}

.back-button {
  background: linear-gradient(135deg, #2e7d32, #4caf50);
  color: white;
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 0.95rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(46, 125, 50, 0.3);
}

.back-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(46, 125, 50, 0.4);
}

.restaurant-top {
  display: flex;
  gap: 30px;
  padding: 30px;
  align-items: flex-start;
}

.restaurant-photo {
  flex-shrink: 0;
  width: 300px;
  height: 200px;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}

.restaurant-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.restaurant-photo:hover img {
  transform: scale(1.05);
}

.restaurant-info {
  flex: 1;
}

.restaurant-info h1 {
  margin: 0 0 8px 0;
  color: #2d3748;
  font-size: 2.2rem;
  font-weight: 700;
  background: linear-gradient(135deg, #2e7d32, #4caf50);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.cuisine-type {
  color: #718096;
  margin: 0 0 20px 0;
  font-size: 1.1rem;
  line-height: 1.5;
}

.rating {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #fff9c4;
  padding: 8px 16px;
  border-radius: 20px;
  width: fit-content;
  box-shadow: 0 2px 8px rgba(255, 193, 7, 0.3);
}

.rating-icon {
  font-size: 1.2rem;
}

.rating-value {
  font-weight: 700;
  color: #f57c00;
}

.rating-count {
  color: #666;
  font-size: 0.9rem;
}

/* Main Content Layout */
.restaurant-content {
  display: grid;
  grid-template-columns: 1fr 350px;
  gap: 30px;
  align-items: start;
}

/* Menu Section */
.menu-section {
  background: white;
  border-radius: 16px;
  padding: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.menu-section h2 {
  margin: 0 0 25px 0;
  color: #2d3748;
  font-size: 1.8rem;
  font-weight: 600;
  position: relative;
  padding-bottom: 10px;
}

.menu-section h2::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 3px;
  background: linear-gradient(90deg, #2e7d32, #4caf50);
  border-radius: 2px;
}

.dishes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
}

.dish-card {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 20px;
  transition: all 0.3s ease;
  border: 1px solid #e9ecef;
  position: relative;
  overflow: hidden;
}

.dish-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #2e7d32, #4caf50);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.dish-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
  background: white;
}

.dish-card:hover::before {
  transform: scaleX(1);
}

.dish-image {
  width: 100%;
  height: 120px;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 15px;
}

.dish-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.dish-card:hover .dish-image img {
  transform: scale(1.1);
}

.dish-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 15px;
}

.dish-info {
  flex: 1;
}

.dish-info h4 {
  margin: 0 0 8px 0;
  color: #2d3748;
  font-size: 1.1rem;
  font-weight: 600;
}

.description {
  color: #718096;
  font-size: 0.9rem;
  line-height: 1.4;
  margin: 0 0 10px 0;
  overflow: hidden;
}

.price {
  color: #2e7d32;
  font-size: 1.2rem;
  font-weight: 700;
  margin: 0;
}

.dish-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
  align-items: center;
}

.add-button {
  background: linear-gradient(135deg, #2e7d32, #4caf50);
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(46, 125, 50, 0.4);
}

.add-button:hover {
  transform: scale(1.1) rotate(90deg);
  box-shadow: 0 6px 20px rgba(46, 125, 50, 0.6);
}

.favorite-button {
  background: #f8f9fa;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  width: 36px;
  height: 36px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.favorite-button:hover {
  background: #ffeb3b;
  border-color: #ffeb3b;
  transform: scale(1.1);
}

/* Cart Section */
.cart-section {
  position: sticky;
  top: 20px;
}

.cart-container {
  background: white;
  border-radius: 16px;
  padding: 25px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  border: 1px solid #e9ecef;
}

.cart-container h3 {
  margin: 0 0 20px 0;
  color: #2d3748;
  font-size: 1.3rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
}

.empty-cart {
  color: #a0aec0;
  text-align: center;
  padding: 40px 20px;
  font-style: italic;
}

.cart-items {
  margin-bottom: 20px;
  max-height: 300px;
  overflow-y: auto;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #f1f3f4;
  transition: background-color 0.2s ease;
}

.cart-item:hover {
  background: #f8f9fa;
  border-radius: 8px;
  padding: 12px;
  margin: 0 -8px;
}

.item-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.item-name {
  color: #2d3748;
  font-weight: 500;
}

.item-price {
  color: #2e7d32;
  font-weight: 600;
  margin-left: 10px;
}

.remove-button {
  background: #fed7d7;
  color: #e53e3e;
  border: none;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  margin-left: 10px;
}

.remove-button:hover {
  background: #e53e3e;
  color: white;
  transform: scale(1.1);
}

.cart-total {
  display: flex;
  justify-content: space-between;
  font-weight: 700;
  font-size: 1.2rem;
  padding: 15px 0;
  border-top: 2px solid #e9ecef;
  margin-bottom: 20px;
  color: #2d3748;
}

.checkout-button {
  background: linear-gradient(135deg, #2e7d32, #4caf50);
  color: white;
  border: none;
  border-radius: 12px;
  padding: 15px;
  width: 100%;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(46, 125, 50, 0.4);
  position: relative;
  overflow: hidden;
}

.checkout-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  transition: left 0.5s;
}

.checkout-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(46, 125, 50, 0.6);
}

.checkout-button:hover::before {
  left: 100%;
}

/* Empty States */
.empty-menu {
  grid-column: 1 / -1;
  text-align: center;
  padding: 60px 20px;
  color: #a0aec0;
}

.empty-menu p {
  font-size: 1.1rem;
  margin: 0;
}

/* Loading and Error States */
.loading, .error {
  text-align: center;
  padding: 80px 20px;
  font-size: 1.2rem;
  background: white;
  border-radius: 16px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  margin: 20px 0;
}

.loading {
  color: #4caf50;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.loading::after {
  content: '';
  width: 40px;
  height: 40px;
  border: 4px solid #e9ecef;
  border-top: 4px solid #4caf50;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.error {
  color: #e53e3e;
  background: #fed7d7;
}

/* Responsive Design */
@media (max-width: 968px) {
  .restaurant-content {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .cart-section {
    position: static;
    order: -1;
  }
  
  .restaurant-top {
    flex-direction: column;
    text-align: center;
  }
  
  .restaurant-photo {
    width: 100%;
    max-width: 400px;
    margin: 0 auto;
  }
  
  .rating {
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .restaurant-page {
    padding: 15px;
  }
  
  .restaurant-top {
    padding: 20px;
  }
  
  .menu-section {
    padding: 20px;
  }
  
  .dishes-grid {
    grid-template-columns: 1fr;
  }
  
  .dish-content {
    flex-direction: column;
    align-items: stretch;
  }
  
  .dish-actions {
    flex-direction: row;
    justify-content: space-between;
    margin-top: 15px;
  }
}

@media (max-width: 480px) {
  .restaurant-info h1 {
    font-size: 1.8rem;
  }
  
  .menu-section h2 {
    font-size: 1.5rem;
  }
  
  .cart-container {
    padding: 20px;
  }
}