.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  background-color: #2e7d32;
  color: white;
  padding: 15px 0;
  position: sticky;
  top: 0;
  z-index: 100;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  gap: 20px;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 20px;
  flex: 1;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  white-space: nowrap;
}

.header-search {
  flex: 1;
  max-width: 400px;
  display: flex;
  position: relative;
}

.header-search input {
  flex: 1;
  padding: 8px 40px 8px 15px;
  border: none;
  border-radius: 20px;
  font-size: 1rem;
  outline: none;
}

.header-search button {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search-icon {
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 20px;
}

.nav-links {
  display: flex;
  gap: 15px;
}

.nav-links span {
  cursor: pointer;
  white-space: nowrap;
  transition: opacity 0.3s;
}

.nav-links span:hover {
  opacity: 0.8;
}

.contact-info {
  font-weight: bold;
  white-space: nowrap;
}

.login-btn {
  background: transparent;
  border: 1px solid white;
  border-radius: 20px;
  color: white;
  padding: 6px 12px;
  display: flex;
  align-items: center;
  gap: 5px;
  cursor: pointer;
  transition: all 0.3s;
}

.login-btn:hover {
  background: rgba(255,255,255,0.1);
}

.main-content {
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  width: 100%;
}

.app-footer {
  background-color: #f5f5f5;
  padding: 30px 0;
  border-top: 1px solid #e0e0e0;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 30px;
}

.footer-logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #2e7d32;
  cursor: pointer;
}

.footer-links {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.footer-links span {
  cursor: pointer;
  transition: color 0.3s;
}

.footer-links span:hover {
  color: #2e7d32;
}

.footer-contact {
  font-weight: bold;
}

.copyright {
  grid-column: 1 / -1;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #e0e0e0;
  color: #666;
  font-size: 0.9rem;
}

/* Адаптивность */
@media (max-width: 768px) {
  .header-container {
    flex-direction: column;
    gap: 15px;
  }
  
  .header-left, .header-right {
    width: 100%;
  }
  
  .header-search {
    max-width: 100%;
  }
  
  .nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .footer-container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
}

.auth-buttons {
  display: flex;
  gap: 10px;
  align-items: center;
}

.cart-btn {
  background: #ff9800;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: background-color 0.3s;
}

.cart-btn:hover {
  background: #f57c00;
}

.login-btn {
  background: #2e7d32;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: background-color 0.3s;
}

.login-btn:hover {
  background: #1b5e20;
}

.logout-btn {
  background: #f44336;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  transition: background-color 0.3s;
}

.logout-btn:hover {
  background: #d32f2f;
}

/* Загрузка приложения */
.app-loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 1.2rem;
}

.loading-spinner {
  font-size: 2rem;
  margin-bottom: 20px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

/* Адаптивность для header */
@media (max-width: 768px) {
  .auth-buttons {
    flex-direction: column;
    gap: 5px;
  }
  
  .cart-btn,
  .login-btn,
  .logout-btn {
    padding: 6px 12px;
    font-size: 0.9rem;
  }
}