.about-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  font-family: 'Arial', sans-serif;
}

.about-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 20px;
}

.hero-section {
  background: linear-gradient(135deg, #2e7d32 0%, #1b5e20 100%);
  color: white;
  padding: 80px 20px;
  text-align: center;
  border-radius: 15px;
  margin-bottom: 40px;
}

.hero-content h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: white;
}

.hero-content p {
  font-size: 1.2rem;
  opacity: 0.9;
}

.company-section,
.values-section,
.team-section,
.contact-section {
  background: white;
  padding: 60px 0;
  margin-bottom: 40px;
  border-radius: 15px;
  box-shadow: 0 5px 20px rgba(0,0,0,0.1);
}

.company-section h2,
.values-section h2,
.team-section h2,
.contact-section h2 {
  text-align: center;
  color: #2e7d32;
  font-size: 2.2rem;
  margin-bottom: 40px;
}

.company-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 40px;
  align-items: center;
}

.company-text p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 20px;
  color: #555;
}

.company-stats {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.stat-item {
  text-align: center;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 10px;
}

.stat-number {
  display: block;
  font-size: 2rem;
  font-weight: bold;
  color: #2e7d32;
  margin-bottom: 5px;
}

.stat-label {
  color: #666;
  font-size: 0.9rem;
}

.values-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  margin-top: 40px;
}

.value-card {
  text-align: center;
  padding: 30px;
  background: #f8f9fa;
  border-radius: 15px;
  transition: transform 0.3s ease;
}

.value-card:hover {
  transform: translateY(-5px);
}

.value-icon {
  font-size: 3rem;
  margin-bottom: 20px;
}

.value-card h3 {
  color: #2e7d32;
  margin-bottom: 15px;
}

.value-card p {
  color: #666;
  line-height: 1.5;
}